.footer__title {
  font-family: 'Inter', 'Arial', 'sans-serif';
  font-style: normal;
  font-weight: 400;
  font-size: 18px;
  line-height: 22px;
  color: #545454;
  max-width: 318px;
  margin: 0;
}

@media screen and (max-width: 600px) {
  .footer__title {
    font-size: 14px;
    line-height: 17px;
    max-width: 282px;
  }
}